@media screen and (max-width: 576px) {
  .logo-container > div > img {
    width: 80%;
  }
  .logo-container > *,
  .nav-utils {
    text-align: center;
  }
}
@media screen and (max-width: 768px) and (min-width: 576px) {
  .logo-container {
    flex: 0 0 58.333333%;
  }
  .nav-btn-group,
  .nav-utils {
    flex: 0 0 100%;
  }
  .logo-container > *,
  .nav-utils {
    text-align: center;
  }
}
@media screen and (max-width: 992px) and (min-width: 768px) {
  .logo-container,
  .nav-utils {
    flex: 0 0 41.666667%;
  }
  .nav-btn-group {
    flex: 0 0 16.666667%;
  }
}
@media screen and (max-width: 1200px) and (min-width: 992px) {
  .logo-container,
  .nav-btn-group,
  .nav-utils {
    flex: 0 0 33.333333%;
    width: 33.333333%;
  }
  .nav-btn-group > div > button,
  .telegram-button-container > button {
    margin: 10px;
    padding: 6px 20px;
    font-size: 1rem;
  }
}
@media screen and (min-width: 1200px) {
  .logo-container,
  .nav-btn-group,
  .nav-utils {
    flex: 0 0 33.333333%;
    width: 33.333333%;
  }
  .logo-container > div {
    max-width: 91.666667%;
  }
  .logo-container > div > img {
    width: 70%;
  }
  .nav-btn-group > div > button,
  .telegram-button-container > button {
    margin: 10px;
    padding: 6px 20px;
    font-size: 1rem;
  }
}

.dropdown-btn::after {
  content: "";
  height: 0;
  width: 0;
  vertical-align: 0.255em;
  margin-left: 0.255em;
  display: inline-block;
  border-bottom: 0;
  border-top: 0.3em solid;
  border-left: 0.3em solid transparent;
  border-right: 0.3em solid transparent;
  flex: 1 1;
}

.dropdown-container {
  z-index: 99;
  min-width: 10rem;
  margin: 0.125rem 0 0;
  font-size: 1rem;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
}
.dropdown-item {
  display: block;
  padding: 0.25rem 1.5rem;
  background-color: transparent;
  color: #212529;
}
.dropdown-item:hover {
  background-color: rgba(0, 0, 0, 0.02);
}

.counter-container {
  height: 36px;
  width: 36px;
  margin: 10px;
}

.counter-path,
.counter-progress {
  stroke-dasharray: 300px, 300px;
}
.counter-path {
  stroke-dashoffset: 0;
  stroke: #d6d6d6;
  stroke-linecap: round;
}
.counter-progress {
  stroke: #3dc6c1;
  transition: stroke-dashoffset 500ms ease;
}
.counter-text {
  fill: #3dc6c1;
  font-size: 44px;
}

@media screen and (max-width: 576px) {
  .counter-container {
    height: 25px;
    width: 25px;
  }
}

@media screen and (max-width: 768px) and (min-width: 576px) {
  .counter-container {
    height: 25px;
    width: 25px;
  }
}

