.theme-btn-container {
  margin: 10px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #f8f9fa;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #3dc6c1;
  transition: 0.4s;
}

.dark-theme input:checked + .slider {
  background-color: #2e3241;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.statistics-container {
  padding: 0 30px;
}
.statistics-container > div {
  padding: 10px 0;
}

.side-info > p {
  margin: 0;
  white-space: nowrap;
}
.side-info > p:first-child {
  color: #5dc7c2;
  font-size: 40px;
}
.side-info > p:nth-child(2) {
  opacity: 0.45;
  font-size: 20px;
}

.middle-info {
  padding-bottom: 30px;
}
.middle-info > p:first-child {
  font-size: 28px;
  letter-spacing: 0.75px;
}
.middle-info > p:nth-child(2) {
  font-size: 80px;
  padding-bottom: 10px;
}
.middle-info > p:last-child {
  letter-spacing: 0.75px;
  font-size: 16px;
  opacity: 0.45;
}

@media screen and (max-width: 576px) {
  .side-info > p:first-child,
  .middle-info > p {
    font-size: 14px;
  }
  .side-info > p:nth-child(2) {
    font-size: 10px;
  }
  .middle-info {
    padding-bottom: 15px;
  }
  .middle-info > p:nth-child(2) {
    font-size: 20px;
  }
  .middle-info > p:last-child {
    font-size: 8px;
  }
}
@media screen and (max-width: 768px) and (min-width: 576px) {
  .side-info > p:first-child,
  .middle-info > p {
    font-size: 14px;
  }
  .side-info > p:nth-child(2) {
    font-size: 10px;
  }
  .middle-info {
    padding-bottom: 15px;
  }
  .middle-info > p:nth-child(2) {
    font-size: 20px;
  }
  .middle-info > p:last-child {
    font-size: 8px;
  }
}
@media screen and (max-width: 992px) and (min-width: 768px) {
  .middle-info > p:first-child {
    font-size: 22px;
  }
  .middle-info > p:nth-child(2) {
    font-size: 60px;
  }
  .middle-info > p:last-child {
    font-size: 12px;
  }
}

.add-to-home-container {
