}

.trade-table-container {
  padding: 0 25px;
  overflow-x: auto;
}
.trade-table {
  border-collapse: separate;
  border-spacing: 0 1em;
  min-width: 540px;
}
.trade-table span {
  cursor: pointer;
}
.trade-table th h4 {
  white-space: nowrap;
  font-size: 24px;
  letter-spacing: 1px;
  opacity: 0.45;
  font-weight: 500;
  line-height: 1.2;
  margin: 0;
}
.trade-table tbody tr {
  background-color: #f8f9fa;
}
.trade-table tbody tr td:first-child {
  border-radius: 10px 0 0 10px;
}
.trade-table tbody tr td:last-child {
  border-radius: 0px 10px 10px 0px;
}
.exchange-name p {
  padding-left: 15px;
}
.trade-table td {
  padding: 15px 12px;
  color: black;
}
.trade-table td h4 {
  white-space: nowrap;
  text-align: center;
  margin: 0;
  line-height: 1.2;
  font-size: 24px;
  font-weight: 500;
}
.currency-logo {
  height: 24px;
  width: 24px;
}
.cell-red {
  color: #da5757 !important;
}
.cell-green {
  color: #5dc7c2 !important;
}

@media screen and (max-width: 576px) {
  .trade-table-container {
    padding: 0 10px;
  }
  .trade-table th h4 {
    font-size: 10px;
  }
  .trade-table td {
    padding: 6px 3px;
  }
  .trade-table td h4 {
    font-size: 14px;
  }
}
@media screen and (max-width: 768px) and (min-width: 576px) {
  .trade-table-container {
    padding: 0 10px;
  }
  .trade-table th h4 {
    font-size: 10px;
  }
  .trade-table td {
    padding: 6px 3px;
  }
  .trade-table td h4 {
    font-size: 14px;
  }
}

